import React, { useState, useEffect } from 'react';
import styles from './inquiry.module.css';
import { InputGroup, FormControl } from 'react-bootstrap';
import { useSelector,useDispatch } from 'react-redux';
import { AppTable } from '../../../../common/components';
import { CSVLink } from 'react-csv';
import {LoadInquiries} from '../../../../general_redux/inquiry/action';
import Loading from '../../../../components/loading';

function Inquiry() {
  const rentals = useSelector(({ rentals }) => rentals && rentals.rentals);
  const user= useSelector(({user})=> user && user.user);
  //const inquiryList= useSelector(({inquiry})=>inquiry && inquiry.inquiryData);
  const [dateFrom, setDateFrom] = useState('');
  const [dateTo, setDateTo] = useState('');
  const [rental, setRental] = useState('');
  const dispatch = useDispatch();

  const inquiryStatus = useSelector(({inquiry})=>inquiry && inquiry.actionForInquiries);
  const queryResult = useSelector((state) => state.inquiry.inquiryData);
  const dateFormat = useSelector(({generalSetting})=>generalSetting && generalSetting.setting.dateDisplay);
  const TimeFormat =useSelector(({generalSettings})=>generalSettings && generalSettings.setting.timeDisplay);
  function clearFilter() {
    setDateFrom('');
    setDateTo('');
    setRental('');
  }

  useEffect(() => {
   dispatch(LoadInquiries());
  }, [])

  useEffect(() => {
    const payload={
      customerId:user.id,
      dateFrom: dateFrom,
      dateTo: dateTo,
      rentalID: rental
    }
    console.log(payload);
    //dispatch the inquiry filter API to load data
  }, [rental,dateFrom,dateTo]);

  const cols = [
    { lable: 'Booking Nr', key: 'bookingNumber' },
    { lable: 'Channel', key: 'channel' },
    { lable: 'Rental', key: 'rentalId' },
    { lable: 'Guest Name', key: 'guestName' },
    { lable: 'Check-in', key: 'checkInTime' },
    { lable: 'Check-out', key: 'checkOutTime' },
    { lable: 'Nights', key: 'nights' },
    { lable: 'Net Amount', key: 'netAmount' },
    { lable: 'Created', key: 'created' },
    { lable: 'Payment Status', key: 'payment_status' },
    { lable: 'Status', key: 'status' },
  ];

  const headers = [
    { label: 'Booking Nr', key: 'bookingNumber' },
    { label: 'Channel', key: 'channel' },
    { label: 'Rental', key: 'rentalId' },
    { label: 'Guest Name', key: 'guestName' },
    { label: 'Check-in', key: 'checkInTime' },
    { label: 'Check-out', key: 'checkOutTime' },
    { label: 'Nights', key: 'nights' },
    { label: 'Net Amount', key: 'netAmount' },
    { label: 'Created', key: 'created' },
    { label: 'Payment Status', key: 'payment_status' },
    { label: 'Status', key: 'status' },
  ];

  //=================================
  // SAMPLE QUERY RESULT DATA
  //=================================

  return (
    <>
    <Loading loadingStatus={inquiryStatus.loading}/>
    <div className={styles.wrapper}>
      <div className={styles.header}>
        <div className={styles.filter_container}>
          <div className={styles.filter_filter_name}>
            <i className={`fa fa-filter ${styles.filter_icon}`}></i>{' '}
            <span className={styles.filter_title_header}>Filter</span>
          </div>
          <div className={styles.filter_sub_container}>
            <InputGroup className={`m-1 ${styles.input_group}`}>
              <InputGroup.Prepend>
                <InputGroup.Text className={styles.filter_title}>
                  Date from:
                </InputGroup.Text>
              </InputGroup.Prepend>
              <FormControl
                onChange={(e) => setDateFrom(e.target.value)}
                className={styles.filter_entry}
                id="from"
                aria-describedby="Search from"
                type="date"
                value={dateFrom}
              />
            </InputGroup>

            <InputGroup className={`m-1 ${styles.input_group}`}>
              <InputGroup.Prepend>
                <InputGroup.Text className={styles.filter_title}>
                  Date to:
                </InputGroup.Text>
              </InputGroup.Prepend>
              <FormControl
                onChange={(e) => setDateTo(e.target.value)}
                className={styles.filter_entry}
                id="to"
                aria-describedby="search to"
                type="date"
                value={dateTo}
                disabled={dateFrom ? false : true}
                min={dateFrom}
              />
            </InputGroup>

            <InputGroup className={`m-1 ${styles.input_group}`}>
              <InputGroup.Prepend>
                <InputGroup.Text className={styles.filter_title}>
                  Select rental:
                </InputGroup.Text>
              </InputGroup.Prepend>

              <select
                onChange={(e) => setRental(e.target.value)}
                className={styles.filter_entry}
                value={rental}
              >
                <option value="" selected disabled hidden>
                  Select Rental
                </option>
                {rentals &&
                  rentals.map((r) => <option value={r.id}>{r.name}</option>)}
              </select>
            </InputGroup>
          </div>
          <div className={styles.btn_group}>
            <div className={`my-2 ${styles.btn_download}`}>
              <button className={`${styles.btn} w-25`} onClick={clearFilter}>
                Clear Filter
              </button>
            </div>
            <div className={`my-2 ${styles.btn_download}`}>
              <CSVLink
                className={`w-25 ${styles.btn_csv} ${styles.btn}`}
                data={queryResult}
                headers={headers}
                filename={'BeeHaz_inquiry.csv'}
              >
                Download CSV
              </CSVLink>
            </div>
          </div>
        </div>
      </div>
      <div className={`m-3`}>
        <AppTable 
          cols={cols} 
          tableName="Inquiry" 
          rows={queryResult} 
          dateFormat={dateFormat}
        />
      </div>
    </div>
    </>
  );
}

export default Inquiry;
