import React, { useState, useEffect } from 'react';
import { Modal } from 'react-bootstrap';
import moment from 'moment';
import { useSelector, useDispatch } from 'react-redux';
import { isMobile } from 'react-device-detect';
import SlideSelect from '../../../../components/Slide_select/Slide_select';
import MyCalendar from '../../../../components/calendar';
import NewBooking from './components/NewBooking/AddNewBooking';
import BlockDate from './components/BlockDate/BlockDate';
import {
  addNewBooking,
  editBooking,
  loadBooking,
  resetBookingFlag,
  clearBooking,
} from '../../../../general_redux/calendar/actions';
import { addGuestByBooking } from '../../../../general_redux/guest/actions';
import { addGuests, loadGuests, resetGuestFlag } from '../../../../general_redux/guest/actions';
import { loadRental } from '../../../../general_redux/rentals/actions';
import { resetUser } from '../../../../general_redux/user/actions';
import { rateList } from '../../../../general_redux/rates/actions';
import ViewBooking from './components/ViewBooking/ViewBooking';
import WelcomeModal from './components/WelcomeScreen/Welcome';
import Loading from '../../../../components/loading';
import 'react-big-calendar/lib/css/react-big-calendar.css';
import styles from './calendar.module.css';

const Calendar = (props) => {
  const rentals = useSelector(({ rentals }) => rentals && rentals.rentals).map((row) => ({
    id: row.id,
    title: row.name,
  }));
  const items = useSelector(({ calendar }) => calendar && calendar.allBooking);
  const userStatus = useSelector(({ user }) => user && user.isNewUser);
  const itemStatus = useSelector(({ calendar }) => calendar && calendar.actionForBooking);
  const actionForRental = useSelector(({ rentals }) => rentals && rentals.actionForRental);
  const actionForGuest = useSelector(({ guests }) => guests && guests.actionForLoad);
  const actionForRate = useSelector(({ rates }) => rates && rates.actionForRateList);
  const bookingFlag = useSelector(({ calendar }) => calendar && calendar.bookingFlag);
  const guestFlag = useSelector(({ guests }) => guests && guests.guestFlag);
  const addBookingStatus = useSelector(({ calendar }) => calendar && calendar.actionForAddBooking);
  const dispatch = useDispatch();

  // rentals = rentals.map((d) => ({
  //   ...groups.find(({ id }) => id === d.group),
  //   ...d,
  // }));

  const now = new Date().getUTCFullYear();
  const years = Array(now - (now - 20))
    .fill('')
    .map((v, idx) => now - idx)
    .reverse();
  const Months = [
    'January',
    'February',
    'March',
    'April',
    'May',
    'June',
    'July',
    'August',
    'September',
    'October',
    'November',
    'December',
  ];
  const days = ['YesterDay', 'Today', 'Tomorrow'];
  const d = new Date();
  const [currentSelectMonth,setCurrentSelectMonth] = useState(d.getMonth());
  const [currentSelectYear,setCurrentSelectYear]= useState(years.length - 1);
  const [timeLineDate, setTimeLineDate] = useState({ year: years.length - 1, month: d.getMonth(), day: 1 });
  const [viewBooking, setViewBooking] = useState(false);
  const [addBooking, setAddBooking] = useState(false);
  const [blockDate, setBlockDate] = useState(false);
  const [welcomeModal, setWelcomeModal] = useState(false);
  const [viewData, setViewData] = useState(null);
  const [edited, setEdited] = useState(null);
  const [selectedDate, setSelectedDate] = useState(new Date());
  const [isNewGuest, setIsNewGuest] = useState(false);
  const [guestData, setGuestData] = useState([]);
  const [isEdit, setIsEdit] = useState(false);

  const handleSaveBooking = (data, guestData, isNewGuest) => {
    if (edited) {
      dispatch(editBooking(data));
    } else {
      dispatch(addNewBooking(data));
      if (isNewGuest) {
        setIsNewGuest(true);
      } else {
        setIsNewGuest(false);
      }
      setGuestData(guestData);
    }
    setAddBooking(false);
    setBlockDate(false);
    setIsEdit(false);
    setEdited(null);
  };
  const selectBooking = (id) => {
    console.log(id);
    const viewItem = items.filter((item) => item.id === id)[0];
    if (viewItem.type === 'booking') {
      setViewData(viewItem);
      setViewBooking(true);
    }
  };
  const handleEdit = () => {
    setIsEdit(true);
    setEdited(viewData);
    setViewBooking(false);
    setAddBooking(true);
  };

  const selectDate = (data, type) => {
    let { year, month, day } = timeLineDate;
    const before = moment(selectedDate).format('D M YYYY').split(" ");
    if (type === 1 && year !== data) {
      setTimeLineDate({ year: data, month: before[1]-1, day});
    } else if (type === 2 && month !== data) {
      setTimeLineDate({ year, month: data, day });
    } else if (type === 0 && day !== data) {
      const today=moment().format('D M YYYY').split(" ");
      const yearIndx = years.findIndex((year)=>year===Number(today[2]));
      setTimeLineDate({ year:yearIndx , month: today[1]-1 , day: data });
      setCurrentSelectMonth(today[1]-1);
      setCurrentSelectYear(yearIndx)
    }
  };
  const onCloseWelcome = () => {
    dispatch(resetUser());
    setWelcomeModal(false);
  };

  const totalDays = isMobile ? 3 : 10;

  useEffect(() => {
    dispatch(loadGuests());
    dispatch(loadRental());
    dispatch(loadBooking());
    dispatch(rateList());
    if (userStatus) {
      setTimeout(() => {
        setWelcomeModal(true);
      }, 1000);
    }
  }, []);

  useEffect(() => {
    if (addBookingStatus.success) {
      if (bookingFlag) {
        if (isNewGuest) {
          dispatch(addGuests(guestData));
          setIsNewGuest(false);
        } else {
          dispatch(addGuestByBooking(guestData, bookingFlag));
          dispatch(clearBooking());
          dispatch(resetBookingFlag());
        }
      }
    }
  }, [dispatch, bookingFlag]);

  useEffect(() => {
    if (guestFlag) {
      if (addBookingStatus.success) {
        const guestData1 = {
          guestId: guestFlag,
        };
        console.log('IF:', guestData1);
        dispatch(addGuestByBooking(guestData1, bookingFlag));
        dispatch(clearBooking());
        dispatch(resetBookingFlag());
      }
      dispatch(resetGuestFlag());
    }
  }, [dispatch, guestFlag]);

  useEffect(() => {
    if (timeLineDate) {
      const year = years[timeLineDate.year];
      const month = timeLineDate.month;
      const getDay = timeLineDate.day === 0 ? -1 : timeLineDate.day === 1 ? 0 : 1;
      const today = new Date();
      today.setDate(today.getDate() + getDay);
      today.setMonth(month);
      today.setFullYear(year);
      setSelectedDate(today.getTime());
    }
  }, [timeLineDate]);

  const onTimeChange = (visibleTimeStart,visibleTimeEnd, updateScrollCanvas) => {
    const today = new Date(visibleTimeStart);
    today.setDate(today.getDate() + totalDays);
    const scrollStart = moment(visibleTimeStart).format("D M YYYY");
    const timeArray = scrollStart.split(" ");
    const indx= years.findIndex((year)=>year === Number(timeArray[2]));
    setCurrentSelectMonth(timeArray[1]-1);
    setCurrentSelectYear(indx);
    setSelectedDate(today.getTime());
    updateScrollCanvas(visibleTimeStart, visibleTimeEnd)
  };
  return (
    <>
      <Loading
        loadingStatus={actionForRental.loading || actionForGuest.loading || itemStatus.loading || actionForRate.loading}
      />
      <div className={styles.calendarHeader}>
        <div className={styles.calendarSetup}>
          <SlideSelect list={years} currentSelect={currentSelectYear} onSelectData={(index) => selectDate(index, 1)} />
          <SlideSelect list={Months} currentSelect={currentSelectMonth} onSelectData={(index) => selectDate(index, 2)} />
          <SlideSelect list={days} currentSelect={1} onSelectData={(index) => selectDate(index, 0)} />
        </div>
        <WelcomeModal show={welcomeModal} onHide={onCloseWelcome} />
        <Modal
          show={addBooking}
          size="lg"
          aria-labelledby="contained-modal-title-vcenter"
          centered
          onHide={() => {
            setAddBooking(false);
            setEdited(null);
            setIsEdit(false);
          }}
        >
          <NewBooking
            handleSave={(data, guestData, check) => handleSaveBooking(data, guestData, check)}
            viewForEdit={edited}
            isEdit={isEdit}
          />
        </Modal>

        <Modal
          onHide={() => {
            setBlockDate(false);
          }}
          show={blockDate}
          size="md"
          aria-labelledby="contained-modal-title-vcenter"
          centered
        >
          <BlockDate handleSave={(data) => handleSaveBooking(data)} />
        </Modal>

        <div className={styles.flexEnd}>
          <button
            className={`${styles.allBtn} w-25`}
            onClick={() => setAddBooking(true)}
            disabled={rentals.length === 0}
          >
            Add Booking
          </button>
          <button
            className={`${styles.allBtn} w-25`}
            onClick={() => setBlockDate(true)}
            disabled={rentals.length === 0}
          >
            Block Dates
          </button>
        </div>
      </div>
      <MyCalendar
        groups={rentals}
        items={items}
        clickItem={(itemId) => selectBooking(itemId)}
        selectedDate={selectedDate}
        onTimeChange={onTimeChange}
        totalDays={totalDays}
      />
      <ViewBooking
        show={viewBooking}
        onHide={() => {
          setViewBooking(false);
          setEdited(null);
        }}
        data={viewData ? viewData : []}
        onEdit={() => console.log('Editing')}
      />
    </>
  );
};

export default Calendar;
