import React from 'react';
import {useSelector,useDispatch} from "react-redux";
import {Modal} from 'react-bootstrap';
import * as yup from 'yup';

import DefaultChangeForm from "./DefaultChangeForm";
import {editRates} from "../../../../../../general_redux/rates/actions";
import styles from '../../../Rentals/rentals.module.css';
import { toast } from 'react-toastify';

function BulkDefaultChange(props){
    const dispatch=useDispatch();
    const rates=useSelector(({rates})=>rates && rates.rateSettings);

    const schemaDefaultRate = yup.object({
        guestFee: yup.number().required(),
        dailyRate: yup.number(),
        discount: yup.boolean(),
        fixRate: yup.boolean(),
        guestLimit: yup.number(),
        minStay: yup.number().required(),
        monthDiscount: yup.number(),
        monthPrice: yup.number(),
        rentalOrGroup: yup.string().required(),
        weekDiscount: yup.number(),
        weekPrice: yup.number(),
    });
    const rentals = useSelector(({rentals}) => rentals && rentals.rentals);

    const findRateId= (rentalId)=>{
        let id=null;
        const ar = rates.filter((row)=>row.rentalId===Number(rentalId));
        if(ar.length!==0){
            id=ar[0].id;
        }
        return id;
    }
    const handleSubmit=(data)=>{
        const ToSendId=findRateId(data.rentalOrGroup);
        console.log(ToSendId);
        if(ToSendId){
            const payload={
                id: ToSendId,  
                rentalId:data.rentalOrGroup,
                usdPerGuest:data.guestFee,
                minimumStayRequirement:data.minStay,
                dailyRate:data.dailyRate,
                guestPerNight:data.guestLimit,
                allowDiscount:data.discount,
                weeklyDiscount:data.weekDiscount,
                monthlyDiscount:data.monthDiscount,
                allowFixedRate:data.fixRate,
                weekPrice:data.weekPrice,
                monthlyPrice:data.monthPrice,
            }
            dispatch(editRates(payload));
        }else{
            toast.error("Couldn't update Rate!")
        }
        props.onHide();
    }
    return(
        <Modal
            {...props}
            size="lg"
            aria-labelledby="contained-modal-title-vcenter"
            centered
        >
            <Modal.Header closeButton>
                <Modal.Title id="contained-modal-title-vcenter">
                    Bulk default Rates
                </Modal.Title>
            </Modal.Header>
            <Modal.Body className={styles.deleteModalBody}>
                <DefaultChangeForm
                    schema={schemaDefaultRate}
                    handleSubmit={handleSubmit}
                    options={rentals.map((row)=>({id:row.id,name:row.name}))}
                />
            </Modal.Body>
        </Modal>
    )
}
export default BulkDefaultChange;