import React from 'react';
import {Button,Form} from 'react-bootstrap';

import MySelect from "../../../Rentals/components/AddEditRental/MySelect";
import countryList from "react-select-country-list";

function AddGuestForm(props){
    const {
        values,
        touched,
        errors,
        handleChange,
        setFieldValue,
        handleSubmit,
        isSubmitting,
        setFieldTouched
    } = props;

    return (
        <Form onSubmit={handleSubmit}>
            <Form.Row>
                <Form.Label className="col-sm-4 col-form-label">Guest Name</Form.Label>
                <div className="col-md-4">
                    <Form.Control
                        type="text"
                        name="name"
                        onChange={handleChange}
                        isInvalid={!!errors.name }
                        value={values.name}
                    />
                    <Form.Control.Feedback type="invalid">{errors.name}</Form.Control.Feedback>
                </div>
            </Form.Row>
            <br/>
            <Form.Row>
                <Form.Label className="col-sm-4 col-form-label">Company</Form.Label>
                <div className="col-md-4">
                    <Form.Control
                        type="text"
                        name="company"
                        onChange={handleChange}
                        isInvalid={!!errors.company }
                        value={values.company}
                        //defaultValue={(initValue)?initValue.company:""}
                    />
                    <Form.Control.Feedback type="invalid">{errors.company}</Form.Control.Feedback>
                </div>
            </Form.Row>
            <br/>
            <Form.Row>
                <Form.Label className="col-sm-4 col-form-label">Primary email</Form.Label>
                <div className="col-md-4">
                    <Form.Control
                        type="text"
                        name="emailId"
                        onChange={handleChange}
                        isInvalid={!!errors.emailId }
                        value={values.emailId}
                        //defaultValue={(initValue)?initValue.emailId:""}
                    />
                    <Form.Control.Feedback type="invalid">{errors.emailId}</Form.Control.Feedback>
                </div>
            </Form.Row>
            <br/>
            <Form.Row>
                <Form.Label className="col-sm-4 col-form-label">Secondary Email</Form.Label>
                <div className="col-md-4">
                    <Form.Control
                        type="text"
                        name="secondaryEmailId"
                        onChange={handleChange}
                        isInvalid={!!errors.secondaryEmailId }
                        value={values.secondaryEmailId}
                        //defaultValue={(initValue)?initValue.secondaryEmailId:""}
                    />
                    <Form.Control.Feedback type="invalid">{errors.secondaryEmailId}</Form.Control.Feedback>
                </div>
            </Form.Row>
            <br/>
            <Form.Row>
                <Form.Label className="col-sm-4 col-form-label">Phone number</Form.Label>
                <div className="col-md-4">
                    <Form.Control
                        type="number"
                        name="phoneNo"
                        onChange={handleChange}
                        isInvalid={!!errors.phoneNo }
                        value={values.phoneNo}
                        //defaultValue={(initValue)?initValue.phoneNo:""}
                    />
                    <Form.Control.Feedback type="invalid">{errors.phoneNo}</Form.Control.Feedback>
                </div>
            </Form.Row>
            <br/>
            <Form.Row>
                <Form.Label className="col-sm-4 col-form-label">Select Country</Form.Label>
                <div className={"col-md-4"}>
                    <MySelect
                        value={values.country}
                        onChange={setFieldValue}
                        onBlur={setFieldTouched}
                        error={errors.country}
                        touched={touched.country}
                        optionArray={countryList().getData()}
                        optFor={"country"}
                    />
                </div>
            </Form.Row>
            <br/>
            <Form.Row>
                <Form.Label className="col-sm-4 col-form-label">State</Form.Label>
                <div className="col-md-4">
                    <Form.Control
                        type="text"
                        name="state"
                        onChange={handleChange}
                        isInvalid={!!errors.state }
                        value={values.state}
                    />
                    <Form.Control.Feedback type="invalid">{errors.state}</Form.Control.Feedback>
                </div>
            </Form.Row>
            <br/>
            <Form.Row>
                <Form.Label className="col-sm-4 col-form-label">Postal Code</Form.Label>
                <div className="col-md-4">
                    <Form.Control
                        type="number"
                        name="postalCode"
                        onChange={handleChange}
                        isInvalid={!!errors.postalCode }
                        value={values.postalCode}
                        //defaultValue={(initValue)?initValue.postalCode:""}
                    />
                    <Form.Control.Feedback type="invalid">{errors.postalCode}</Form.Control.Feedback>
                </div>
            </Form.Row>
            <br/>
            <Form.Row>
                <Form.Label className="col-sm-4 col-form-label">Nationality</Form.Label>
                <div className="col-md-4">
                    <Form.Control
                        type="text"
                        name="nationality"
                        onChange={handleChange}
                        isInvalid={!!errors.nationality }
                        value={values.nationality}
                    />
                    <Form.Control.Feedback type="invalid">{errors.nationality}</Form.Control.Feedback>
                </div>
            </Form.Row>
            <br/>
            <Form.Row>
                <Form.Label className="col-sm-4 col-form-label">Language</Form.Label>
                <div className="col-md-4">
                    <Form.Control
                        type="text"
                        name="language"
                        onChange={handleChange}
                        isInvalid={!!errors.language }
                        value={values.language}
                    />
                    <Form.Control.Feedback type="invalid">{errors.language}</Form.Control.Feedback>
                </div>
            </Form.Row>
            <br/>
            <Form.Row>
                <Form.Label className="col-sm-4 col-form-label">Notes</Form.Label>
                <div className="col-md-4">
                    <Form.Control
                        type="text"
                        name="notes"
                        onChange={handleChange}
                        isInvalid={!!errors.notes }
                        value={values.notes}
                    />
                    <Form.Control.Feedback type="invalid">{errors.notes}</Form.Control.Feedback>
                </div>
            </Form.Row>
            <br/>
            <div className={`{styles.modalFooter} p-2 d-flex justify-content-center `}>
                <Button variant="primary" disabled={isSubmitting} type="submit" className={'col-lg-4 p-2 mr-4 col-sm-12'}>Save changes</Button>
            </div>
        </Form>
    );

}

export default AddGuestForm;
