import React, { useEffect, useState } from 'react';
import styles from './settings.module.css';
import {useSelector, useDispatch} from 'react-redux';
import Loading from "../../../../components/loading";
import {setSettings,LoadSettings} from '../../../../general_redux/general settings/actions';
import Select from 'react-select';
import {currencies} from '../../../../config/data/currencies';
import {Row,Col} from 'react-bootstrap';

function Settings() {
  const {setting, dateFormatArray,numberFormatArray,actionForSettings,actionForSettingsEdit} = useSelector(({generalSetting}) => generalSetting && generalSetting);
  const user= useSelector(({user})=>user && user.user);
  const [defaultCurrency, setDefaultCurrency] = useState(setting.currency);
  const [timeFormat, setTimeFormat] = useState(setting.timeDisplay);
  const [dateFormat, setDateFormat] = useState(setting.dateDisplay);
  const [numberFormat, setNumberFormat] = useState(setting.numberDisplay);
  const dispatch = useDispatch();

  useEffect(()=>{
    dispatch(LoadSettings());
  },[])

  useEffect(()=>{
    setTimeFormat(setting.timeDisplay);
    setDateFormat(setting.dateDisplay);
    setNumberFormat(setting.numberDisplay);
    setDefaultCurrency(setting.currency);
  },[setting])

  // Handle Save
  function handleSave() {
    const payload={
      emailId:user.emailId,
      name:user.name,
      currency:(defaultCurrency)?defaultCurrency.label:"USD",
      timeDisplay:timeFormat,
      dateDisplay:dateFormat,
      numberDisplay:findNumberFormat(numberFormat),
    }
    console.log(payload);
    dispatch(setSettings(payload));
  }
  const findDateFormat=(datef)=>{
    const index=dateFormatArray.filter((row)=>{
      if(row.val===datef){
        return row;
      }
    })
    return index[0].id;
  }
  const findNumberFormat = (numf)=>{
    const index=numberFormatArray.filter((row)=>{
      if(row.val===numf){
        return row;
      }
    })
    return index[0].id;
  }

  return (
    <div className={'p-3'}>
      <Loading loadingStatus={actionForSettings.loading ||
          actionForSettingsEdit.loading}/>
      <h2>General Settings</h2>
      <div className={styles.settings_wrapper}>
        <div className={`input-group mb-3 ${styles.input_group}`}>
          <div className="input-group-prepend">
            <label
              className={`input-group-text ${styles.input_group_text}`}
              htmlFor="propertyType"
            >
              Default Currency
            </label>
          </div>
          {/* <select
            onChange={(e) => setDefaultCurrency(e.target.value)}
            className={`custom-select ${styles.form_control}`}
            id="defaultCurrency"
            value={currencyArray.filter((c) => c === defaultCurrency)}
          >
            <option value='' disabled={true}>Select Default Currency</option>
            {currencyArray && currencyArray.map((c) => <option value={c} selected={(defaultCurrency===c)}>{c}</option>)}
          </select> */}
          <Select
            options={currencies}
            value={defaultCurrency}
            onChange={(val)=>setDefaultCurrency(val)}
            className={`custom-select ${styles.form_entry}`}
          />
        </div>

        <div className={`input-group mb-3 ${styles.input_group}`}>
          <div className="input-group-prepend">
            <label
              className={`input-group-text ${styles.input_group_text}`}
              htmlFor="timeFormat"
            >
              Time Format
            </label>
          </div>
          <select
            onChange={(e) => setTimeFormat(e.target.value)}
            className={`custom-select ${styles.form_control}`}
            id="timeFormat"
            value={timeFormat}
          >
          <option value=''>Select Time Format</option>
            <option value="AM_PM" selected={(timeFormat==="AM_PM")}>AM / PM</option>
            <option value="H" selected={(timeFormat==="H")}>24 H</option>
          </select>
        </div>

        <div className={`input-group mb-3 ${styles.input_group}`}>
          <div className="input-group-prepend">
            <label
              className={`input-group-text ${styles.input_group_text}`}
              htmlFor="dateFormat"
            >
              Date Format
            </label>
          </div>
          <select
            onChange={(e) => setDateFormat(e.target.value)}
            className={`custom-select ${styles.form_control}`}
            id="dateFormat"
            value={dateFormat}
          >
          <option value=''>Select Date Format</option>
            {dateFormatArray && dateFormatArray.map((t) => <option value={t.id} selected={t.id===dateFormat} key={t.id}>{t.val}</option>)}
          </select>
        </div>
        <Row>
          <Col md={4} xs={"auto"}>
            <div className="input-group-prepend ">
              <label
                  className={`input-group-text ${styles.input_group_text}`}
                  htmlFor="numberFormat"
              >
                Number Format
              </label>
            </div>
          </Col>
          <Col md={8} xs={4}>
            <Row >
              <div className={"col-xs-auto col-md-auto"} >
                <input type="radio" value={"1'000.00"} checked={numberFormat==="1'000.00"} onChange={(e)=>setNumberFormat(e.target.value)} name={"numberFormat"}/>
                <label htmlFor="1">1'000.00</label>
              </div>
              <div className={"col-xs-auto col-md-auto"} >
                <input type="radio" value={"1,000.00"} checked={numberFormat==="1,000.00"} onChange={(e)=>setNumberFormat(e.target.value)} name={"numberFormat"}/>
                <label htmlFor="2">1,000.00</label>
              </div>
              <div className={"col-xs-auto col-md-auto"}>
                <input type="radio" value={"1.000,00"} checked={numberFormat==="1.000,00"} onChange={(e)=>setNumberFormat(e.target.value)} name={"numberFormat"} />
                <label htmlFor="3">1.000,00</label>
              </div>
            </Row>
          </Col>
        </Row>

        <div className="d-flex justify-content-center">
          <button className={styles.settings_save} onClick={handleSave}>
            Save Changes
          </button>
        </div>
      </div>
    </div>
  );
}

export default Settings;
